            </span>
          </template>
        </el-table-column>
        <el-table-column prop="bai_m_caption" label="活动主标题" align="center" show-overflow-tooltip width="150">
        </el-table-column>
        <el-table-column prop="bai_start_time" label="活动开始时间" align="center" show-overflow-tooltip width="150">
          <template slot-scope="scope">

