              {{scope.row.bai_start_time.substring(0, scope.row.bai_start_time.length - 3)}}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="bai_end_time" label="活动结束时间" align="center" show-overflow-tooltip width="150">
          <template slot-scope="scope">

