          </template>
        </el-table-column>
        <el-table-column prop="bai_num_count" label="投放数量" align="center" show-overflow-tooltip width="120">
          <template slot-scope="scope">
            <el-tag size="small" effect="dark">{{ scope.row.bai_num_count }}</el-tag>
          </template>
        </el-table-column>
