            <el-tag size="small" effect="dark" type="success">{{ scope.row.get_count }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="use_count" label="使用数量" align="center" show-overflow-tooltip width="120">
          <template slot-scope="scope">
            <el-tag size="small" effect="dark" type="info">{{ scope.row.use_count }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="join_count" label="邀请人数" align="center" show-overflow-tooltip width="120">
