            <el-tag v-if="scope.row.bai_status==='2'" size="small" effect="dark" type="success">已上线</el-tag>
            <el-tag v-if="scope.row.bai_status==='3'" size="small" effect="dark" type="info">已暂停</el-tag>
            <el-tag v-if="scope.row.bai_status==='5'" size="small" effect="dark" type="danger">已过期</el-tag>
            <el-tag v-if="scope.row.bai_status==='1'" size="small" effect="dark" type="warning">草稿箱</el-tag>
          </template>
        </el-table-column>
