            <el-button v-if="bai_status === '2'&&scope.row.bai_user_scope=='1'" @click.native.stop="customer_phone1(scope.$index, scope.row)" type="text"
              size="small" style="color: rgb(168, 168, 168);">添加指定客户手机</el-button>
            <el-button v-if="bai_status === '2'" @click.native.stop="modifying_merchant(scope.$index, scope.row)" type="text"
