            <el-button v-if="bai_status === '2'||bai_status === '5'" @click.native.stop="event_detail(scope.$index, scope.row)" type="text"
              size="small">查看活动规则详情</el-button>
            <el-button v-if="bai_status === '2'" @click.native.stop="modify_coupons_number(scope.$index, scope.row)" type="text"
              size="small">修改标准优惠券投放数量</el-button>
            <el-button v-if="scope.row.bai_status === '1'" @click.native.stop="continue_editing(scope.$index, scope.row)" type="text"
              size="small">继续编辑</el-button>
            <el-button v-if="bai_status === '2'||bai_status === '5'" @click.native.stop="export_pipeline(scope.$index, scope.row)" type="text"
              size="small">导出商户对账单</el-button>
            <el-button v-if="bai_status === '2'" @click.native.stop="ModifyInformation(scope.$index, scope.row)" type="text"
              size="small">修改活动基础信息</el-button>
            <el-button v-if="scope.row.bai_status === '2'&&bai_status=='2'" @click.native.stop="suspend(scope.$index, scope.row,1)" type="text"
              size="small">暂停活动</el-button>
            <el-button v-if="scope.row.bai_status === '3'&&bai_status=='2'" @click.native.stop="switchClick(scope.$index, scope.row,2)" type="text"
              size="small">启动活动</el-button>
            <!-- <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="registration_review(scope.$index, scope.row)">活动报名审核</el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="customer_phone(scope.$index, scope.row)">添加指定客户手机</el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="modifying_merchant(scope.$index, scope.row)">修改商户范围</el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="event_detail(scope.$index, scope.row)">查看活动规则详情</el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="modify_coupons_number(scope.$index, scope.row)">修改标准优惠券投放数量
              </el-button>
            </div>
            <div v-if="scope.row.bai_status === '1'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="continue_editing(scope.$index, scope.row)">继续编辑
              </el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="export_pipeline(scope.$index, scope.row)">导出商户对账单</el-button>
            </div>
            <div v-if="bai_status === '2'" style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="ModifyInformation(scope.$index, scope.row)">修改活动基础信息
              </el-button>
            </div>
            <div v-if="scope.row.bai_status === '2'&&bai_status=='2'"
              style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="suspend(scope.$index, scope.row,1)">暂停活动
              </el-button>
            </div>
            <div v-if="scope.row.bai_status === '3'&&bai_status=='2'"
              style="width: 100%;margin: 5px 0;text-align: center">
              <el-button style="width: 80%" @click="switchClick(scope.$index, scope.row,2)">启动活动
              </el-button>
            </div> -->
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-pagination style="margin-top: 30px" background @size-change="handleSizeChange"
      @current-change="handleCurrentChange" :current-page="queryParam.currentPage" :page-sizes="[10, 20, 50, 100,200]"
      :page-size="queryParam.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryParam.totalRow">
    </el-pagination>

