          <el-table-column prop="bsi_name" label="所属机构" align="center" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="bcf_status" label="状态" align="center" show-overflow-tooltip>
            <template slot-scope="scope">
              <el-tag v-if="scope.row.bcf_status==='2'" size="small" effect="dark" type="success">已通过</el-tag>
              <el-tag v-if="scope.row.bcf_status==='1'" size="small" effect="dark" type="info">待审核</el-tag>
            </template>
          </el-table-column>
          <el-table-column v-if="this.bcf_status === '1'" fixed="right" label="操作" align="center">
            <template slot-scope="scope">
              <el-button @click="review_activities(scope.$index, scope.row)">审核</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination style="margin-top: 30px" background @size-change="handleSizeChange1"
          @current-change="handleCurrentChange1" :current-page="queryParam1.currentPage"
          :page-sizes="[10, 20, 50, 100,200]" :page-size="queryParam1.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="queryParam1.totalRow">
        </el-pagination>
      </el-card>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose()">取 消</el-button>
      </div>
    </el-dialog>

    <!--审核报名商户-->
    <el-dialog :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false"
      :visible.sync="dialogVisible1" width="60%">
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '审核内容' }}</span>
      </div>
      <div class="pop-up">
        <el-form :model="couponForm" ref="couponForm" label-width="160px" disabled>
          <el-row :gutter="24" style="margin-bottom: 25px">
            <el-col :md="12">
              <el-form-item label="优惠券金额:" prop="bmti_money_1">
                <el-input style="width: 90%" v-model="this.couponForm.bmti_money_1"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="使用门槛:" prop="bmti_money_2">
                <el-input style="width: 90%" v-model="this.couponForm.bmti_money_2"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 25px">
            <el-col :md="12">
              <el-form-item label="标准优惠券总投放量:" prop="bai_num_count">
                <el-input style="width: 85%" v-model="this.couponForm.bai_num_count"></el-input>
                张
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="优惠券类型:" prop="bmti_use_area">
                <el-select style="width: 90%" clearable placeholder="请选择" v-model="this.couponForm.bmti_use_area">
                  <el-option v-for="item in option" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 25px">
            <el-col :md="24">
              <el-form-item label="用券须知:" prop="bmti_note">
                <el-input v-model="this.couponForm.bmti_note" style="margin-bottom: 20px;width: 96%" type="textarea"
                  autosize :autosize="{ minRows: 2, maxRows: 4}">
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-form :model="auditForm" ref="auditForm" label-width="160px">
          <el-row :gutter="24" style="margin-bottom: 45px">
            <el-col :md="24">
              <el-form-item style="position: relative">
                <el-radio v-model="radio" label="1" style="position: absolute;top: 0;left: 10px;"
                  @change="audit_pass()">审核通过
                </el-radio>
                <el-radio v-model="radio" label="2" style="position: absolute;top: 30px;left: 10px;"
                  @change="audit_no_pass()">审核驳回
                </el-radio>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24" style="margin-bottom: 25px;" v-if="this.auditForm.bcf_status==='4'">
            <el-col :md="24">
              <el-form-item label="原因:" prop="bcf_describer">
                <el-input style="margin-bottom: 20px;width: 96%" type="textarea" autosize :maxlength="100"
                  v-model="auditForm.bcf_describer" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容,最多支持100个字">
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose7()">取 消</el-button>
        <el-button type="primary" @click="audit_submit()">提 交</el-button>
      </div>
    </el-dialog>

    <!--添加手机号-->
    <el-dialog :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false"
      :visible.sync="dialogVisible2" width="60%">
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '添加指定客户手机' }}</span>
      </div>
      <el-card style="width: 100%">
        <div style="text-align: left;font-size:18px;font-weight: bold;margin-bottom: 20px">
          <span>活动名称：{{ this.bai_m_caption }}</span>
        </div>

        <el-form :model="phoneForm" ref="phoneForm" label-width="80px">
          <el-row :gutter="24">
            <el-col :md="7">
              <el-form-item label="手机号码:" prop="bau_guest_phone">
                <el-input style="width: 150px;" :maxlength="11" v-model="phoneForm.bau_guest_phone"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="6">
              <el-form-item label="次数:" prop="bau_get_num">
                <el-input style="width: 50px;" :maxlength="2" v-model.number="phoneForm.bau_get_num"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="4">
              <el-button style="margin-right: 25px" type="primary" @click="phone_Add('phoneForm')">录入</el-button>
            </el-col>
            <el-col :md="3">
              <el-upload ref="uploadFile" :action="this.uploadConfig.actionUrl" :with-credentials='true'
                :show-file-list="true" :limit="1" accept=".xlsx" :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload" :on-exceed="selUploadFile">
                <!--                :data="{jo:JSON.stringify(this.phone_value)}"-->
                <el-button type="primary">导入</el-button>
              </el-upload>
            </el-col>
            <el-col :md="3">
              <el-button type="primary">
                <el-link style="text-decoration: none;color: #FFFFFF" :href="href">模板下载</el-link>
              </el-button>
            </el-col>
          </el-row>


          <el-table :data="tableData_phone" @sort-change="sortColumn" style="margin-left: 120px;width: 80%;"
            class="account-plat-fixed-right account-plat-fixed " border ref="platformUserTable" tooltip-effect="dark"
            :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
            :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }">
            <el-table-column fixed align="center" type="index" label="序号" width="50">
            </el-table-column>
            <el-table-column prop="bau_guest_phone" label="手机号码" align="center" show-overflow-tooltip width="150">
            </el-table-column>
            <el-table-column prop="bau_get_num" label="次数" align="center" show-overflow-tooltip width="150">
