<script>
  import Bus from "../../../../utils/Bus";
  import shareBackground from "../../../../components/views/components/shareBackground.vue"
  import vueCrroper from "../../../../components/views/components/vueCrroper.vue"

  export default {
    name: "CreditCardActivity",
    destroyed() {
      Bus.$off('globalMsgForActivityQuery');
    },
    components:{shareBackground,vueCrroper},
    created: function () {
      this.href = this.customGlobal.serverUrl.orgBaseURI + "fr/imgserver/admin/initdata.xlsx";
      console.log("this.href:" + this.href);
      let that = this;
      Bus.$on('globalMsgForActivityQuery', function (val) {//监听各组件传递的 globalMsg 事件
        // console.log("监听到的消息："+JSON.stringify(val));
        console.info("val:" + JSON.stringify(val));
        if (val.flag === '1') {
          that.bai_status = '2';
          that.queryParam.currentPage = 1;
          that.Select_Activity();
        } else if (val.flag === '2') {
          that.bai_status = '1';
          that.queryParam.currentPage = 1;
          that.Select_Activity();
        }


      });
      console.log("url参数：" + JSON.stringify('hah' + this.pri_detail.info));

      console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).act_id);
      console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).flag);
      this.bai_status = '2';
      this.queryParam.currentPage = 1;
      this.Select_Activity();
      this.shareMsg = {
          bsi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          bsi_province_id: that.customGlobal.userInfoExt.cui_province_id,
          bsi_city_id: that.customGlobal.userInfoExt.cui_city_id,
          bsi_county_id: that.customGlobal.userInfoExt.cui_county_id,
          bsi_station_id: that.customGlobal.userInfoExt.cui_station_id,
        }
    },
    mounted: function () {
      // console.info("BankActivity.vue mounted get route params :"+ JSON.stringify(this.$route.params));
      // console.info("BankActivity.vue mounted get route query :"+ JSON.stringify(this.$route.query));


    },
    data() {
      let bmti_num_count1 = (rule, value, callback) => {
        if (value.trim().length === 0) {
          callback(new Error('增加数量不能为空'));
        } else if (this.customGlobal.isnumber(value) === false) {
          callback(new Error('增加数量必须是数字'));
        } else {
          callback();
        }
      };
      let that = this
      return {
        pickerOptionscreate: {
          disabledDate(time) { // 开始时间的禁用
            if (that.dayMsg1.bai_end_time) {
              return time.getTime() < new Date(that.dayMsg1.bai_end_time).getTime()
            }
          }
        },
        textarea1: '',
        shareMsg: {},
        shareShow: false,
        vueCrroperShow: false,
        shareIndex: '',
        dayMsg: {},
        dayMsg1: {},
        href: '',
        show1: false,
        activeIndex: '1',
        activeIndex1: '1',
        queryParam: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        tableData: [],
        bai_status: '',
        dialogVisible: false,
        dialogVisible1: false,
        dialogVisible12: false,
        dialogVisible15: false,
        tableData_shop: [],
        tableData_shop1: [],
        queryParam1: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        activity: '',
        bcf_status: '',
        radio: '1',
        couponForm: {},
        auditForm: {
          bcf_describer: '',
          bcf_status: ''
        },
        tableData_coupon: [],
        activity_status: '',
        shop_id1: '',
        option: [{
          value: '1',
          label: '全场通用',
        }],
        bcf_uuid: '',
        bcf_type: '',
        bcf_cui_uuid: '',
        bcf_cui_type: '',
        dialogVisible2: false,
        bai_m_caption: '',
        tableData_phone: [],
        queryParam2: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        bai_uuid: '',
        phoneForm: {
          bau_guest_phone: '',
          bau_get_num: '1'
        },
        dialogVisible3: false,
        shopForm: {},
        radio4: '1',
        shop_tableData: [],
        queryParam3: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        dialogVisible4: false,
        for_merchantsForm: {
          bmi_industry_id: '',
          bmi_mer_name: '',
        },
        tableData_industry: [],//行业表格数据
        multipleSelection: [],//table选中行内容
        tableData1: [],
        queryParam4: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        dialogVisible5: false,
        couponForm1: {},
        dialogVisible6: false,
        numberForm: {
          bmti_num_count1: ''
        },
        numberFormrules: {
          bmti_num_count1: [
            { required: true, validator: bmti_num_count1, trigger: 'blur' }
          ],
        },
        phone_value: {},
        uploadConfig: {
          actionUrl: this.customGlobal.serverUrl.orgBaseURI + "/fr/postCallPowerFileUpload?userid=" + this.customGlobal.userInfoExt.cui_login_id + "&logic=1&jo=" + JSON.stringify(this.phone_value),
          uploadImgFlag: false,
          uploadPercent: 0,
          uploadImageUrl: '',
        },
        bmti_ticket_id: '',
        bmti_ticket_standard_id: ''

      }
    },
    props: {
      //获取url参数
      pri_detail: {
        type: Object,
        default: function () {
          return {
            info: {}
          }

        }
      }
    },
    methods: {
      //打开小程序分享弹窗
      share_background(index, row) {
        this.shareShow = !this.shareShow
        if (this.$refs.shareRef) {
          this.$refs.shareRef.getData()
        }
      },
      // 截图组件返回值
      vueCrroper(val, img) {
        this.vueCrroperShow = val
        this.shareShow = !val
        if (img) {
          this.$refs.shareRef.submitImg(img)
          this.$refs.shareRef.getData()
        }
      },
      // 调用组件删除
      deleteImg() {
        this.$refs.shareRef.deleteImg()
      },
      confirmClick() {
        let that = this;
        let _jsondata_ = {
          bai_uuid: this.dayMsg.bai_uuid, //活动ID
          bai_m_caption: this.dayMsg.bai_m_caption, //活动主标题
          bai_s_caption: this.dayMsg.bai_s_caption, //活动副标题
          bai_describer: this.dayMsg.bai_describer, //活动内容描述
          bai_start_time: this.dayMsg.bai_start_time,//活动开始时间(原活动开始时间不能改变)
          bai_end_time: this.dayMsg.bai_end_time,  //活动结束时间
          bai_cui_uuid_modify: that.customGlobal.userInfoExt.cui_uuid,//银行人员
        };
        const loading = this.$loading({
