          bmi_station_id: that.customGlobal.userInfoExt.cui_station_id,
          perPageSize: that.queryParam4.pageSize,
          currentPage: that.queryParam4.currentPage,
        };

        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a7 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Bank_invite_merchants_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a7.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.tableData_shop1 = res.data.rows;
            that.queryParam4.totalRow = res.data.total;
            loading.close();
            that.tableData1.forEach(function (value, index) {
              console.log('右侧商户ID：' + value.bmi_uuid + ",index:" + index);
              var r_id = value.bmi_uuid;
              that.tableData_shop1.forEach(function (value1, index) {
                console.log('左侧商户ID：' + value1.bmi_uuid + ",index:" + index);
                if (value1.bmi_uuid === r_id) {
                  console.log("删除左侧商户ID：" + r_id);
                  that.tableData_shop1.splice(index, 1);
                }
              })
            })
            // /////////////
            // this.$message({
            //   type: 'success',
            //   showClose: true,
            //   duration: 3000,
            //   dangerouslyUseHTMLString: true,
            //   message: '<span style="color:black;">商户查询成功</span>'
            // });
          } else {
            that.tableData_shop1 = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }

        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.tableData_shop1 = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.tableData_shop1 = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
